import React, { useState, useEffect } from 'react';
import {Typography} from '@material-ui/core'
import alanBtn from '@alan-ai/alan-sdk-web';
import wordsToNumbers from 'words-to-numbers';
import NewsCards from './components/NewsCards/NewsCards';

import useStyles from './styles.js';
import Image from './images/5.png';
import logo from './images/alan-seal-horizontal-color.png'

const alanKey = '96f402aea8e1a023397f41a30b2025dd2e956eca572e1d8b807a3e2338fdd0dc/stage';

const App = () => {
    const [newsArticles, setNewsArticles] = useState([]);
    const [activeArticle, setActiveArticle] = useState(-1);
    const classes = useStyles();

useEffect(() => {
    alanBtn({
        key: alanKey,
        onCommand: ({command, articles, number}) => {
            if(command === 'newHeadlines'){
                console.log(articles);
              setNewsArticles(articles);
              setActiveArticle(-1);
            }else if(command === 'highlight'){
                setActiveArticle((previousActiveArticle) => previousActiveArticle + 1 );
            }else if(command === 'open'){
                const parsedNumber = number.length > 2 ? wordsToNumbers(number, { fuzzy: true}) : number;
                const article = articles[parsedNumber - 1];

                if(parsedNumber > 20){
                    alanBtn().playText('Please ask me again.')
                }else if(article){
                    window.open(article.url, '_blank');
                    alanBtn().playText('There you go!')
                }


            }
      

        }
    })
}, [])


    return(
        <div>
            <div className={classes.imageContainer}>
            {newsArticles.length ? (
          <div className={classes.infoContainer}>
            <div className={classes.card}><Typography variant="h5" component="h2">Try saying: <br /><br />Open article number [4]</Typography></div>
            <div className={classes.card}><Typography variant="h5" component="h2">Try saying: <br /><br />Go back</Typography></div>
          </div>
        ) : null}
                <img src={Image} className={classes.newsImage} alt="news app logo" />
            </div>
            <NewsCards articles={newsArticles} activeArticle = {activeArticle}/>
   
        <div className={classes.footer}>
          <Typography variant="body1" component="h2">
            &copy;{new Date().getFullYear()} Created by 
            <a className={classes.link} href="https://github.com/Aggy86">Aga Boloz</a> 
           
          </Typography>
          <img className={classes.image} src={logo} height="50px" alt="alan logo" />
        </div>
     
        </div>
    )
}

export default App;