import React from 'react';
//creating user interface with material ui - see styles.js
import { Grid, Grow, Typography } from '@material-ui/core';
import useStyles from './styles.js';
import NewsCard from '../NewsCard/NewsCard';

const NewsCards = ({articles, activeArticle}) => {
    const classes = useStyles();
    const infoCards = [
        { color: '#57BC90', title: 'Latest News & Weather', text: 'Give me the latest news. Tell me the weather for tomorrow.' },
        { color: '#4B4A67', title: 'News by Categories', info: 'Business, Entertainment, General, Health, Science, Sports, Technology', text: 'Give me the latest Technology news' },
        { color: '#015249', title: 'News by Terms', info: 'COVID-19, Cats, AI, Donald Trump, Poland...', text: 'What\'s up with Cats?' },
        { color: '#A5A5AF', title: 'News by Sources', info: 'BBC News, CNN, Wired, Independent, Buzzfeed, Al-Jazeera', text: 'Give me the news from BBC News' },
      ];

    if(!articles.length){
        return(
            <Grow in>
                 <Grid className={classes.container} container alignItems="stretch" spacing={3}>
               {/* map and loop over infoCards  */}
                {infoCards.map((infoCard) => (
                    <Grid item xs={12} sm={6} md={4} lg={3} className={classes.infoCard}> 
                    <div className={classes.card} style={{backgroundColor: infoCard.color}}>
                        <Typography variant="h4">{infoCard.title}</Typography>
                        { infoCard.info ?(<Typography variant="h5">
                                    <strong>
                                        {infoCard.title.split(' ')[2]}:
                                    </strong>
                                    <br/>
                                        {infoCard.info}
                                 </Typography>) : null}
                                 <Typography variant="h6">Try saying: <br/> <i>{infoCard.text}</i> </Typography>
                    </div>

                    </Grid>
                ))} 
                </Grid>
            </Grow>
        );
    }
    return (
        <Grow in>
            <Grid className={classes.container} container alignItems="stretch" spacing={3}>
            {articles.map((article, i) => (
                <Grid key={i} item xs={12} sm={6} md={4} lg={3} style={{display: "flex"}}>
                     {/* passing specific article as props */}
               <NewsCard article={article} activeArticle={activeArticle} i={i} /> 
              
               </Grid>
           ))}
            </Grid>
          
        </Grow>
    );
}

export default NewsCards;
